<template>
  <div class="infoContainer">
    <div class="top">
      <div>
        <i :style="{backgroundImage: 'url(' + logoUrl + ')'}"></i>
        <span>{{ systemName || '智能安全生产管控平台系统后台' }}</span>
      </div>
      <div>
        <i @click="toindex"></i>
        <!-- <i>
          <el-badge is-dot class="item1"></el-badge>
        </i>-->
        <i @click="toinformation">
          <el-badge :is-dot="hasNews" class="item2"></el-badge>
        </i>
        <i>
          <el-dropdown>
            <span class="el-dropdown-link"></span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <button class="btns" @click="changeName = true">修改姓名</button>
                <el-dialog title="修改姓名" :visible.sync="changeName" append-to-body width="30%">
                  <el-form :model="form1">
                    <el-form-item label="姓名:" :label-width="formLabelWidth" required>
                      <el-input v-model="form1.name" autocomplete="off" placeholder="admin"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="changeName = false">取 消</el-button>
                    <el-button type="primary" @click="changeName = false;updateName();">确 定</el-button>
                  </div>
                </el-dialog>
              </el-dropdown-item>
              <el-dropdown-item>
                <button class="btns" @click="changePassword = true">修改密码</button>
                <el-dialog title="修改密码" :visible.sync="changePassword" append-to-body width="30%">
                  <el-form :model="form2">
                    <el-form-item label="原密码:" :label-width="width" required>
                      <el-input v-model="form2.old" autocomplete="off" placeholder="请输入原密码"></el-input>
                    </el-form-item>
                    <el-form-item label="新密码:" :label-width="width" required>
                      <el-input
                        v-model="form2.new"
                        autocomplete="off"
                        placeholder="请输入新密码，6-20位，数字、字母、字符组成"
                      ></el-input>
                    </el-form-item>
                    <el-form-item label="重复密码:" :label-width="width" required>
                      <el-input v-model="form2.again" autocomplete="off" placeholder="再次输入密码"></el-input>
                    </el-form-item>
                  </el-form>
                  <div slot="footer" class="dialog-footer">
                    <el-button @click="changePassword = false">取 消</el-button>
                    <el-button type="primary" @click="changePassword = false;updatePassword() ">确 定</el-button>
                  </div>
                </el-dialog>
              </el-dropdown-item>
              <el-dropdown-item>
                <button @click="quit" class="btns">退出登录</button>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </i>
        <i></i>
        <i>{{username}}</i>
      </div>
    </div>
    <div class="centerBody">
      <!-- 百度地图 -->
      <baidu-map
        :center="center"
        :zoom="zoom"
        @ready="handler"
        class="baiduMap"
        @click="getClickInfo"
        :scroll-wheel-zoom="true"
        @zoomend="zoomEnd"
      >
        <bml-marker-clusterer :averageCenter="true">
          <bm-marker
            v-for="(marker, index) of markers"
            :position="{lng: marker.lng, lat: marker.lat}"
            :key="index"
            :title="marker.companyAdress"
            ref="changeStytle"
            :icon="{url: greenIcon, size: {width: 25, height: 32}}"
          >
            <bm-label
              :content="marker.companyName+'——'+marker.companyAdress"
              :position="{lng: marker.lng, lat: marker.lat}"
              :labelStyle="{color: 'white', fontSize : '16px',marginLeft:'40px',backgroundColor:'rgba(21,150,255,0.8)',padding:'15px',border:' 1px solid rgba(1, 28, 49, 0.5)',marginTop:'-20px'}"
              v-if="marker.showla"
            />
          </bm-marker>
          <bm-marker
            v-for="(allStop, index) of allStops"
            :position="{lng: allStop.lng, lat: allStop.lat}"
            :key="index"
            :title="allStop.companyAdress"
            ref="changeStytle"
            :icon="{url: greyIcon, size: {width: 25, height: 32}}"
          >
            <bm-label
              :content="allStop.companyName+'——'+allStop.companyAdress"
              :position="{lng: allStop.lng, lat: allStop.lat}"
              :labelStyle="{color: 'white', fontSize : '16px',marginLeft:'40px',backgroundColor:'rgba(21,150,255,0.8)',padding:'15px',border:' 1px solid rgba(1, 28, 49, 0.5)',marginTop:'-20px'}"
              v-if="allStop.showla2"
            />
          </bm-marker>
        </bml-marker-clusterer>
      </baidu-map>
      <div class="leftForm">
        <div class="left-top">
          <div>
            <div class="title-left">企业概况</div>
            <div class="title-right">全部：{{alltotal}}</div>
          </div>
          <div id="chartOne"></div>
        </div>
        <div class="left-bottom">
          <div class="bottom-t">
            <div>项目列表</div>
            <div>
              <el-radio-group
                v-model="radio"
                fill="#ffffff"
                text-color="#ffffff"
                @change="radioValue"
              >
                <el-radio :label="''" checked>全部</el-radio>
                <el-radio :label="0">合作中</el-radio>
                <el-radio :label="1">暂停合作</el-radio>
              </el-radio-group>
            </div>
          </div>
          <div class="bottom-m">
            <input type="text" placeholder="搜索" v-model="queryContent" id="suggestId" />
            <i class="searchIcon" @click="searchName"></i>
          </div>
          <div class="bottom-b">
            <div>
              <el-table :data="list" style="width: 100%" :show-header="hiddenHeader">
                <el-table-column prop="bid" label="id" v-if="showID"></el-table-column>
                <el-table-column prop="bname" label="企业名称，1行显示" width="200" v-if="showName"></el-table-column>
                <el-table-column prop="address" label="id" v-if="showAdr"></el-table-column>
                <el-table-column fixed="right" label="操作">
                  <template slot-scope="scope">
                    <el-button
                      @click="detail(scope.row,scope.$index); "
                      type="primary"
                      :plain="isplain"
                    >{{scope.row.bname}}</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                small
                layout="prev, pager, next"
                :total="total"
                :page-size="pageSize"
                :current-page="pageNum"
                @current-change="handleCurrentChange"
              ></el-pagination>
            </div>
          </div>
        </div>
      </div>
      <div class="rightForm" v-if="reveal">
        <div class="right">
          <div class="right-top">
            <div class="title-left">项目概况</div>
            <div class="title-right">全部：{{companyAll}}</div>
          </div>
          <div class="right-bottom" id="chartTwo"></div>
        </div>
        <div class="right">
          <div class="right-top">
            <div class="title-left">终端概况</div>
            <div class="title-right">全部：{{terminalAll}}</div>
          </div>
          <div class="right-bottom" id="chartThree"></div>
        </div>
        <div class="right">
          <div class="right-top">
            <div class="title-left">调度员概况</div>
            <div class="title-right">全部：{{yardAll}}</div>
          </div>
          <div class="right-bottom" id="chartFour"></div>
        </div>
      </div>
    </div>
    <el-dialog title="提示" :visible.sync="isPageSet" width="30%">
      <span>暂无页面设置，请先进行页面设置</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isPageSet = false">取 消</el-button>
        <el-button @click="isPageSet = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { BmlMarkerClusterer } from "vue-baidu-map";
import chart1 from "../echarts/chart1.js";
import chart2 from "../echarts/chart2.js";
import chart3 from "../echarts/chart3.js";
import chart4 from "../echarts/chart4.js";
import greyIcon from "../assets/grey.png";
import greenIcon from "../assets/green.png";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("center");
export default {
  name: "TestBaiDu",
  data() {
    return {
      changeName: false,
      form1: {
        name: ""
      },
      formLabelWidth: "60px",
      changePassword: false,
      form2: {
        old: "",
        new: "",
        again: ""
      },
      width: "100px",
      center: { lng: 104, lat: 30 },
      zoom: 6,
      radio: "",
      showID: false,
      showAdr: false,
      showName: false,
      reveal: false,
      queryContent: "",
      alltotal: "",
      longitude: "",
      latitude: "",
      companyList: [],
      markers: [],
      allStops: [],
      stopList: [],
      companyAll: 0,
      terminalAll: 0,
      yardAll: 0,
      logoUrl: "",
      systemName: "",
      hasNews: false,
      isplain: true,
      hiddenHeader: false,
      isPageSet: false,
      greyIcon: greyIcon,
      greenIcon: greenIcon
    };
  },
  async mounted() {
    await this.getAllCompany();
    this.drawChart1();
    this.getLogo();
    this.getNews();
    if (this.hasNews = true) {
      this.newsFlag=true;
    }else{
      this.newsFlag=false;
    }
  },
  computed: {
    ...mapState([
      "pageNum",
      "pageSize",
      "status",
      "list",
      "total",
      "backgroundUrl"
    ]),
    username() {
      return sessionStorage.getItem("name");
    }
  },
  methods: {
    ...mapActions([
      "changeNameAsync",
      "changePasswordAsync",
      "allCompanyAsync",
      "allChartsAsync",
      "allLongitudeAsync",
      "logoMeta",
      "newsStatusAsync"
    ]),
    async getAllCompany() {
      const data = await this.allCompanyAsync();
      this.alltotal = data.alltotal;
      this.cooprate = data.status;
    },
    async getLogo() {
      await this.logoMeta();
      // console.log(this.backgroundUrl)
      if (this.backgroundUrl.code === 210) {
        this.isPageSet = true;
        this.$router.push(`/Index/pageSet`);
      }
      this.systemName = this.backgroundUrl.data.systemName;
      this.logoUrl = this.$download + this.backgroundUrl.data.logoUrl;
    },
    //消息状态
    async getNews() {
      const data = await this.newsStatusAsync();
      console.log(data.length);
      if (data.length > 0) {
        this.hasNews = true;
      } else if (data.length == 0) {
        this.hasNews = false;
      }
    },
    toindex() {
      sessionStorage.setItem("setNewsFl", this.newsFlag);
      this.$router.push(`/Index`);
    },
    quit() {
      this.$router.push(`/Login`);
    
    },
    toinformation() {
      // sessionStorage.setItem("setNewsFl", this.newsFlag);
      this.$router.push(`/Index/informationCenter`);
      this.hasNews = false;
      
    },

    //按名字模糊搜索
    searchName() {
      this.$store.commit("center/initSearch", this.queryContent);
      this.allCompanyAsync();
    },
    //radio状态
    radioValue() {
      this.$store.commit("center/initStatus", this.radio);
      this.allCompanyAsync();
    },
    //分页
    handleCurrentChange(val) {
      this.$store.commit("center/changePage", val);
      this.allCompanyAsync();
    },
    //修改名字
    async updateName() {
      let newName = this.form1.name;
      const data = await this.changeNameAsync({ userName: newName });
      if (data.code === 200) {
        this.form1.name = "";
        this.$message("修改成功");
        this.$router.push(`/Login`);
      } else if (data.code === 202) {
        this.$message("用户名已经存在");
      } else {
        this.$message("修改失败");
      }
    },
    //修改密码
    async updatePassword() {
      let oldword = this.form2.old;
      let newword = this.form2.new;
      let againword = this.form2.again;
      const data = await this.changePasswordAsync({
        oldPwd: oldword,
        newPwd: newword,
        verPwd: againword
      });
      if (data.data.code === 200) {
        this.form2.old = "";
        this.form2.new = "";
        this.form2.again = "";
        this.$message("修改成功");
        this.$router.push(`/Login`);
      } else if (data.data.code === 201) {
        this.form2.old = "";
        this.form2.new = "";
        this.form2.again = "";
        this.$message("三条数据不完整，请全部输入");
      } else if (data.data.code === 202) {
        this.form2.old = "";
        this.form2.new = "";
        this.form2.again = "";
        this.$message("初始密码错误，请重新输入");
      } else if (data.data.code === 203) {
        this.form2.old = "";
        this.form2.new = "";
        this.form2.again = "";
        this.$message("两次输入的密码不一致，请重新输入");
      } else if (data.data.code === 204) {
        this.form2.old = "";
        this.form2.new = "";
        this.form2.again = "";
        this.$message("密码格式错误，请重新输入");
      }
    },
    zoomEnd(e) {
      // console.log(e)
      // this.zoom = e.target.getZoom()
      // console.log(this.zoom);
      //   if (3<this.zoom<8) {
      //   this.showla=false;
      // }else if (this.zoom>7) {
      //   this.showla=true;
      // }
    },
    detail(prop, index) {
      this.bid = prop.bid;
      this.reveal = true;
      this.drawChart2();
      this.drawChart3();
      this.drawChart4();
      this.address = prop.address;
      //点击切换公司位置
      this.center.lng = prop.longitude;
      this.center.lat = prop.latitude;
      //合作中
      let arr = this.markers.reverse();
      for (let i = 0; i < arr.length; i++) {
        arr[i].showla = true;
      }
      this.markers.reverse();
      //暂停合作
      let arr2 = this.allStops.reverse();
      for (let j = 0; j < arr2.length; j++) {
        arr2[j].showla2 = true;
      }
      this.allStops.reverse();
      this.zoom = 20;
    },
    //百度地图
    async handler({ BMap, map }) {
      let mapStyle = { style: "midnight" };
      map.setMapStyle(mapStyle);
      //合作中
      const data1 = await this.allLongitudeAsync({
        status: 0
      });
      this.companyList = data1;
      for (let i = 0; i < this.companyList.length; i++) {
        const position1 = {
          lng: this.companyList[i].longitude,
          lat: this.companyList[i].latitude,
          companyAdress: this.companyList[i].address,
          companyName: this.companyList[i].bname,
          showla: false
        };
        await this.markers.push(position1);
        // console.log(this.markers);
      }
      //暂停合作
      const data2 = await this.allLongitudeAsync({
        status: 1
      });
      this.stopList = data2;
      for (let j = 0; j < this.stopList.length; j++) {
        const position2 = {
          lng: this.stopList[j].longitude,
          lat: this.stopList[j].latitude,
          companyAdress: this.stopList[j].address,
          companyName: this.stopList[j].bname,
          showla2: false
        };
        await this.allStops.push(position2);
        // console.log(this.allStops);
      }
    },
    getClickInfo(e) {
      console.log(e.point.lng);
      console.log(e.point.lat);
      this.center.lng = e.point.lng;
      this.center.lat = e.point.lat;
    },

    //引入图表企业概况
    drawChart1() {
      let firstChart = this.$echarts.init(document.getElementById("chartOne"));
      Object.assign(chart1.data1, this.cooprate);
      // let cooprateChart=chart1.data1[0];
      if (
        chart1.series[0].data[0].value == 0 ||
        chart1.series[0].data[1].value == 0
      ) {
        chart1.series[0].itemStyle.normal.labelLine.show = false;
        chart1.series[0].itemStyle.normal.label.show = false;
      } else {
        chart1.series[0].itemStyle.normal.labelLine.show = true;
        chart1.series[0].itemStyle.normal.label.show = true;
      }
      firstChart.setOption(chart1);
    },
    async drawChart2() {
      let id = this.bid;
      const data = await this.allChartsAsync({
        bid: id
      });
      this.companyAll = data[2].prodata[0].value + data[2].prodata[1].value;

      let secondChart = this.$echarts.init(document.getElementById("chartTwo"));
      Object.assign(chart2.data2, data[2].prodata);

      if (
        chart2.series[0].data[0].value == 0 ||
        chart2.series[0].data[1].value == 0
      ) {
        chart2.series[0].itemStyle.normal.labelLine.show = false;
        chart2.series[0].itemStyle.normal.label.show = false;
      } else {
        chart2.series[0].itemStyle.normal.labelLine.show = true;
        chart2.series[0].itemStyle.normal.label.show = true;
      }
      secondChart.setOption(chart2);
    },
    async drawChart3() {
      let id = this.bid;
      const data = await this.allChartsAsync({ bid: id });
      this.terminalAll = data[0].clidata[0].value + data[0].clidata[1].value;
      let thirdChart = this.$echarts.init(
        document.getElementById("chartThree")
      );
      Object.assign(chart3.data3, data[0].clidata);

      if (
        chart3.series[0].data[0].value == 0 ||
        chart3.series[0].data[1].value == 0
      ) {
        chart3.series[0].itemStyle.normal.labelLine.show = false;
        chart3.series[0].itemStyle.normal.label.show = false;
      } else {
        chart3.series[0].itemStyle.normal.labelLine.show = true;
        chart3.series[0].itemStyle.normal.label.show = true;
      }
      thirdChart.setOption(chart3);
    },
    async drawChart4() {
      let id = this.bid;
      const data = await this.allChartsAsync({ bid: id });
      this.yardAll = data[1].disdata[0].value + data[1].disdata[1].value;
      let fourChart = this.$echarts.init(document.getElementById("chartFour"));
      Object.assign(chart4.data4, data[1].disdata);

      if (
        chart4.series[0].data[0].value == 0 ||
        chart4.series[0].data[1].value == 0
      ) {
        chart4.series[0].itemStyle.normal.labelLine.show = false;
        chart4.series[0].itemStyle.normal.label.show = false;
      } else {
        chart4.series[0].itemStyle.normal.labelLine.show = true;
        chart4.series[0].itemStyle.normal.label.show = true;
      }
      fourChart.setOption(chart4);
    }
  },
  components: {
    BmlMarkerClusterer
  }
};
</script>

